                  @select="handleSelect"
                  prefix-icon="el-icon-search"
                  class="search-input">
                  <div class="search-item" slot-scope="{item }" style="display: flex;justify-content: space-between">
                    <div class="name">{{ item.value }}</div>
                    <span class="comment">{{ item.address}}</span>
                  </div>
                </el-autocomplete>
                <el-button class="search-btn" @click="query" type="primary">提问</el-button>
              </div>
            </div>
            <div class="header-right">
              <router-link to="toanswer" class="question">回答问题</router-link>
              <el-dropdown v-if="isLogin" message placement="top-end" @command="handleCommand">
              <span class="el-dropdown-link messagr-box">
                消息
              </span>
                <el-dropdown-menu slot="dropdown">
                  <el-dropdown-item command="inform">通知</el-dropdown-item>
                  <el-dropdown-item command="invite">邀请</el-dropdown-item>
                  <el-dropdown-item command="like">点赞</el-dropdown-item>
                  <el-dropdown-item command="comment">评论</el-dropdown-item>
                  <el-dropdown-item command="atten">关注</el-dropdown-item>
                </el-dropdown-menu>
              </el-dropdown>
              <el-dropdown @command="handle" v-if="isLogin==true" style="display: flex;align-items: center">
                    <span class="el-dropdown-link">
                      <div @click="touser(userInfo.uid)" class="user" style="cursor: pointer">
                                  <img :src="userInfo.head" alt="">
                                </div>
                    </span>
                <el-dropdown-menu slot="dropdown">
                  <el-dropdown-item command="user">个人中心</el-dropdown-item>
                  <el-dropdown-item command="exit">退出登录</el-dropdown-item>
                </el-dropdown-menu>
              </el-dropdown>
              <div class="tologin" v-else>
                <router-link to="loginmima" class="loginphone">登录</router-link>
                |
                <router-link to="register" class="register">注册</router-link>
              </div>

            </div>
          </el-header>
        </div>
      </div>
      <el-main>
        <keep-alive>
          <router-view v-if="$route.meta.keepAlive"></router-view>
        </keep-alive>
        <router-view v-if="!$route.meta.keepAlive"></router-view>
      </el-main>
      <el-footer>

        <div class="footer m">
          <div class="footer-left">
            <div class="footer-box">
              <div class="link">
                <ul>
                  <li><a href="http://www.jiuniusz.com" target="_blank">玖牛汇</a></li>
                  <li><a href="http://www.jiuniu.club" target="_blank">氢云链</a></li>
                  <li><a href="http://www.95sz.club" target="_blank">盈链科技</a></li>
                  <li><a href="http://www.95car.club" target="_blank">玖伍科技</a></li>
                  <li>
                    <router-link to="about">关于盈链</router-link>
                  </li>
                  <li>
                    <router-link to="copyright">版权声明</router-link>
                  </li>
                  <li>品牌素材</li>
                  <li>使用条款</li>
                  <li>
                    <router-link to="business">商业合作</router-link>
                  </li>
                  <li>申请专栏</li>
                  <li>联系方式</li>
                </ul>
              </div>
              <div class="copy">Copyright 2018-2019.盈链科技(深圳)有限公司 粤ICP被19008008号-1</div>
            </div>
          </div>
          <div class="footer-right">
            <div class="qr weixin">
              <div class="qr-box"><img src="@/images/weixinqr.png" alt=""></div>
              <i class="iconfont iconxinlang2">

              </i>
            </div>
            <div class="qr weibo">
              <div class="qr-box"><img src="@/images/weixinqr.png" alt=""></div>
              <i class="iconfont iconweixin2"></i>
            </div>
          </div>
