      </el-footer>
      <!--弹出框-->
      <el-dialog
        title="提问"
        lock-scroll
        class="dialog1"
        :visible.sync="dialog"
        width="30%"
        center>
        <div class="dialog1-box" v-if="dialog1===true">
          <el-row>
            <span class="add">添加标题</span>
            <span class="dec">(最多40个字)</span>
          </el-row>
          <div class="search-box">
            <el-autocomplete
              v-model="querySearch"
              :fetch-suggestions="querySearchAsync"
              placeholder="请提出问题"
              @select="handleSelect2"
              class="search-input"
              style="width: 100%;"
            ></el-autocomplete>
          </div>
        </div>
        <span v-if="dialog1===true" style="display: flex;justify-content: flex-end" slot="footer" class="dialog-footer">
    <el-button :disabled="ablenext" type="primary" @click="dialog1 = false;dialog2=true">下一步</el-button>
  </span>
        <div class="dialog2-box" v-if="dialog2===true">
          <div class="tit" style="margin-bottom: 10px;">{{querySearch}}</div>
          <el-input
            type="textarea"
            :rows="4"
            placeholder="添加问题回答描述(选填)"
            v-model="textarea">
          </el-input>
          <el-row style="margin: 10px 0;">
            <span class="add">添加图片</span>
            <span class="dec">(最多三张)</span>
          </el-row>
          <el-upload
            class="upload-demo"
            drag
            :limit=3
            name="image"
            action="http://www.yeschains.com/index/Update/upload"
            :on-success="upsuccess"
            :on-exceed="exceed"
            :on-preview="handlePictureCardPreview"
            :before-upload="beforeAvatarUpload"
            :on-remove="handleRemove"
            multiple>
            <i class="el-icon-upload "></i>
            <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
            <!--<div class="el-upload__tip" slot="tip">只能上传jpg/png文件，且不超过500kb</div>-->
          </el-upload>
          <span style="display: flex;justify-content: flex-end" slot="footer" class="dialog-footer"><el-button
            @click="dialog1 = true;dialog2=false">上一步</el-button>
    <el-button type="primary" @click="tosubmit">提交</el-button>
  </span>
