<script>
  import {mapState} from 'vuex'

  export default {
    name: "index", data() {
      return {
        //输入框搜索
        restaurants: [],
        search: '',
        timeout: null,
        tabs: [],
        active: 0,
        article: [],
        recom: [],
        dialog: false,
        dialog1: true,
        dialog2: false,
        querySearch: '',
        ablenext: true,
        textarea: '',
        imgs: [],
        searchlist: []
      };
    },

    computed: mapState(
      ['path', 'isLogin', 'userInfo'],
    ),
    watch: {
      //字节判断
      querySearch(val, old) {
        var len = 0;
        for (var i = 0; i < val.length; i++) {
          var a = val.charAt(i);
          if (a.match(/[^\x00-\xff]/ig) != null) {
            len += 2;
          } else {
            len += 1;
          }
        }
        if (len >= 8) {
          this.ablenext = false;
        } else {
          this.ablenext = true;
        }
      },
    },
    mounted() {
      this.init();
      this.restaurants = this.loadAll();
    },
    methods: {
      //控制页面跳转
      init() {
        this.$store.getters.getUserInfo;
        if (this.path === '/') {
          this.$router.push("/portal");
        } else if (this.path !== '/' || "portal") {
          this.$router.push(this.path); // 页面加载时跳转
        } else {
          this.$router.push("/portal"); // 页面加载时跳转
        }
        this.$post('index/data/question_answer').then(res => {
          this.searchlist = res.data;
        }).catch(err => {
          console.log(err);
        })
      },
      //用户详情
      touser(uid) {
        this.$router.push({name: 'user', query: {otheruid: uid}})
      },
      //消息中心
      handleCommand(command) {
        if (this.isLogin) {
          let uid = this.userInfo.uid;
          this.$router.push({path: 'user', query: {otheruid: uid, type: "message", message: command}})
        } else {
          this.$message('请登录');
          setTimeout(() => {
            this.$router.push('/loginmima');
          }, 1000);
        }
      },
      //个人中心
      handle(commamd) {
        if (commamd == "user") {
          let uid = this.userInfo.uid;
          this.$router.push({path: 'user', query: {otheruid: uid}},)
        } else {
          this.$store.commit("removeSessionStor");
          this.$post('index/data/login_out').then(res => {
          });
          this.$router.push('/portal');
        }
      },
      //提问
      query() {
        if (this.isLogin) {
          this.dialog = true
        } else {
          this.$message('请登录后再提问');
          setTimeout(() => {
            this.$router.push('/loginmima');
          }, 1000);
        }
      },

      //搜索
      toSearch() {
        this.$router.push({path: 'search', query: {search: this.search}});
      },
      //输入框搜索
      loadAll() {
        return [
          {"value": "如何提高自身修养", "address": "68回答"},
          {"value": "戴笠手下的军统四大金刚都是谁？他们最终的结局如何？", "address": "1093回答"},
          {"value": "老祖宗为什么说“穷不走水，富不涉淫”？有何道理？", "address": "0回答"},
          {"value": "比特币到底是什么东西?", "address": "0回答"},
          {"value": "新国际电动车可上路但不实用,怎么解决?", "address": "2回答"},
          {"value": "从血缘关系来说,这个世界上最亲的人是谁", "address": "2回答"},
        ];
      },
      querySearchAsync(queryString, cb) {
        var list = [{}];
        //调用的后台接口

        //从后台获取到对象数组
        this.$post('index/data/sos_question2', {like: this.search}).then((response) => {
          //在这里为这个数组中每一个对象加一个value字段, 因为autocomplete只识别value字段并在下拉列中显示
          // for(let i of response.data){
          //   i.value = i.想要展示的数据;  //将想要展示的数据作为value
          // }
          list = response.data;
          var ans = [];
          if (list.length > 0) {
            list.map(((val, index, array) => {
              let an = {};
              an.value = val.title;
              an.address = val.commentnum + '回答';
              ans.push(an);
            }));
          }
          cb(ans);
        }).catch((error) => {
          console.log(error);
        });
        // var restaurants = this.restaurants;
        // var results = queryString ? restaurants.filter(this.createStateFilter(queryString)) : restaurants;
        // clearTimeout(this.timeout);
        // this.timeout = setTimeout(() => {
        //   cb(results);
        // }, 300 * Math.random());
      },
      createStateFilter(queryString) {
        return (state) => {
          return (state.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
        };
      },
      //选择某一项时
      handleSelect(item) {
        this.$router.push({path: 'search', query: {search: this.search}});
        console.log(item);
      },
      handleSelect2(item) {
        console.log(item);
      },
      //上传成功
      upsuccess(response, file, fileList) {
        this.imgs.push({img: response.url})
      },
      //删除
      handleRemove(file, fileList) {
        var url = file.response.url;
        this.imgs.map((value, index, array) => {
            if (url === value.img) {
              this.imgs.splice(index, 1);
            }
          }
        );

        console.log(file.response.url);
        // console.log(file, fileList);
      },
      handlePictureCardPreview(file) {
        // this.dialogImageUrl = file.url;
        // this.dialogVisible = true;
      },
      beforeAvatarUpload(file) {
        const isJPG = file.type === 'image/jpeg';
        const isLt2M = file.size / 1024 / 1024 < 2;

        // if (!isJPG) {
        //   this.$message.error('上传头像图片只能是 JPG 格式!');
        // }
        // if (!isLt2M) {
        //   this.$message.error('上传头像图片大小不能超过 2MB!');
        // }
        // return isJPG && isLt2M;
      },
      //超出时
      exceed(files, fileList) {
        console.log(fileList);
        // this.upload=false;
        this.$message('最多上传3张图片');
      },
      //问题提交
      tosubmit() {
        /*  this.dialog1 = true;
          this.dialog2 = false;*/
        let title = this.querySearch;
        let uid = this.userInfo.uid;
        let username = this.userInfo.username;
        let summary = this.textarea;
        for (var i = 0; i < this.imgs.length; i++) {
          if (this.imgs.length === 1) {
            var pic1 = this.imgs[i].img;
          } else if (this.imgs.length === 2) {
            var pic1 = this.imgs[i].img;
            var pic2 = this.imgs[i].img;
          } else {
            var pic1 = this.imgs[i].img;
            var pic2 = this.imgs[i].img;
            var pic3 = this.imgs[i].img;
          }
        }

        this.$post('index/data/question_add', {
          title, uid, username, summary, pic1, pic2, pic3
        }).then(res => {
          if (res.data.result === 200) {
            this.$message({type: 'success', message: res.data.msg})
            this.dialog = false;
            this.querySearch = '';
            this.textarea = '';
            this.dialog1 = true;
            this.dialog2 = false;
            this.imgs = [];
            this.$message({type: 'success', message: res.data.msg})
          } else {
            this.$message({type: 'warning', message: res.data.msg})
          }
        })
      }

    }

  }
</script>

