            }
          }
        }
      }


    }
  }

  .el-main {
    margin-top: 60px;
    padding: 0;
  }

  .el-footer {
    margin-top: 50px;
    background-color: #2d3237;
    height: 128px !important;

    .footer {
      height: 100%;
      display: flex;
      justify-content: space-between;

      .footer-left {
        display: flex;
        flex-direction: column;
        justify-content: center;
        color: #59585d;

        .link {
          ul {
            display: flex;
            justify-content: flex-start;
            line-height: 30px;

            li {
              margin-right: 20px;

              a {
                color: #59585d;
              }
            }
          }
        }

        .copy {
          line-height: 30px;
        }
      }

      .footer-right {
        display: flex;
        justify-content: space-between;
        align-items: center;

        .qr {
          width: 50px;
          height: 50px;
          border-radius: 50%;
          display: flex;
          justify-content: center;
          align-items: center;
          background-color: #ccc;
          margin: 0 10px;
